import React from 'react';

const RelatedArtists = ({view}) => {
    const {
        relatedArtists : {
            artists
        }
    } = view;
    console.log(artists)
    const renderArtist = (artist) => {
        return (
            <li>
                {artist.name}
            </li>
        )
    }

    return (
        <div className="related-artists">
            {artists.map(renderArtist)}
        </div>
    )
}

export default RelatedArtists;
